@import url(https://cdn.jsdelivr.net/gh/aritmatica/cdn@main/fonts/satoshi/stylesheet.css);
@font-face {
  font-family: "Material Symbols Outlined";
  font-style: normal;
  font-weight: 100 700;
  src: url(https://cdn.jsdelivr.net/gh/aritmatica/cdn@main/gstatic/icons.woff2) format("woff2");
}
:root {
  color-scheme: dark;
}

.icons {
  font-family: "Material Symbols Outlined";
  font-weight: normal;
  font-style: normal;
  font-size: 1.25em;
  line-height: 0.79;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  vertical-align: top;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  font-feature-settings: "liga";
  -webkit-font-feature-settings: "liga";
  -webkit-font-smoothing: antialiased;
}

* {
  margin: 0;
  padding: 0;
  font-family: satoshi-regular, arial, sans-serif;
  box-sizing: border-box;
}

.fancy-button {
  position: relative;
  cursor: pointer;
  overflow: hidden;
  color: white;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
.fancy-button .flair {
  position: absolute;
  background-color: white;
  aspect-ratio: 1;
  width: 200%;
  pointer-events: none;
  border-radius: 100%;
  will-change: transform;
  z-index: 0;
}
.fancy-button .label {
  font-family: inherit;
  z-index: 0;
  transition: color 0.2s;
}
.fancy-button:hover {
  color: black;
}

body {
  background-color: #0e0f0f;
  color: #FFFFFF;
}

body[show-error] {
  overflow: hidden;
}

#pricing {
  width: 70%;
  margin: 6rem auto 0rem auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media screen and (max-width: 1024px) {
  #pricing {
    width: 90%;
  }
}
#pricing .title {
  font-size: 2.75rem;
  text-align: center;
}
#pricing .title h1 {
  font-family: satoshi-black, arial, sans-serif;
  background: linear-gradient(90deg, #e36970, #7c72ff 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
#pricing .title span {
  font-size: 0.5em;
}
#pricing .plans {
  margin-top: 2rem;
  display: grid;
  gap: 2em;
  grid-template-columns: repeat(3, 1fr);
}
@media screen and (max-width: 1024px) {
  #pricing .plans {
    gap: 1em;
  }
  #pricing .plans div {
    font-size: min(2em, 1.5vw) !important;
  }
  #pricing .plans div .fancy-button {
    width: 100% !important;
    font-size: 1em !important;
  }
}
@media screen and (max-width: 768px) {
  #pricing .plans {
    grid-template-columns: 1fr;
  }
  #pricing .plans div {
    font-size: min(2rem, 4vw) !important;
  }
  #pricing .plans div .fancy-button {
    width: 100% !important;
    font-size: 1.25em !important;
  }
}
#pricing .plans #plan-trial {
