#error-prompt {
  display: none;
}

body[show-error] #error-prompt {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(29, 29, 33, 0.95);
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 2;
  width: 100%;
  height: 100%;
  transition: opacity 500ms;
}
@supports (backdrop-filter: blur()) {
  body[show-error] #error-prompt {
    background-color: rgba(29, 29, 33, 0.4);
    backdrop-filter: blur(0.5rem);
  }
}
body[show-error] #error-prompt .error-content {
  font-size: min(1rem, 2.25vh);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.5em;
  padding-left: 1em;
  padding-right: 1em;
  text-align: center;
}
body[show-error] #error-prompt .error-content .error-mascot {
  height: 16em;
  stroke: black;
  stroke-width: 5em;
}
body[show-error] #error-prompt .error-content .error-title {
  font-size: 4em;
  font-family: satoshi-black, arial, sans-serif;
  font-weight: 600;
}
body[show-error] #error-prompt .error-content .error-description {
  font-size: 1.5em;
  font-family: satoshi-black, arial, sans-serif;
  font-weight: 600;
}
body[show-error] #error-prompt .error-content .error-tip {
  color: gray;
  font-size: 1em;
}
body[show-error] #error-prompt .error-content .error-buttons {
  margin-top: 1em;
  display: flex;
  flex-direction: row;
  gap: 1em;
}
body[show-error] #error-prompt .error-content .error-buttons #error-continue {
  width: 50%;
  background-color: transparent;
  padding: 0.25em 1em 0.25em 1em;
  border: solid #e36970;
  border-radius: 4em;
  font-family: satoshi-bold, arial, sans-serif;
  font-size: 2em;
  text-wrap: nowrap;
  border-color: #e36970;
  color: #e36970;
}
body[show-error] #error-prompt .error-content .error-buttons #error-continue .flair {
  background-color: #e36970;
}
body[show-error] #error-prompt .error-content .error-buttons #error-continue:hover {
  color: #FFFFFF;
}
body[show-error] #error-prompt .error-content .error-buttons #error-go-back {
  width: 50%;
  background-color: transparent;
  padding: 0.25em 1em 0.25em 1em;
  border: solid #3fb950;
  border-radius: 4em;
  font-family: satoshi-bold, arial, sans-serif;
  font-size: 2em;
  text-wrap: nowrap;
  border-color: #3fb950;
  color: #3fb950;
}
body[show-error] #error-prompt .error-content .error-buttons #error-go-back .flair {
  background-color: #3fb950;
}
body[show-error] #error-prompt .error-content .error-buttons #error-go-back:hover {
  color: #FFFFFF;
}
@media screen and (max-width: 768px) {
  body[show-error] #error-prompt .error-content .error-buttons {
    flex-direction: column;
  }
  body[show-error] #error-prompt .error-content .error-buttons .fancy-button {
    width: auto !important;
  }
}